<template>
  <div class="page">
    <van-nav-bar
        title="面试刷题平台"
        fixed
    />

    <div class="search">
      <van-field
          readonly
          clickable
          label="分类"
          :value="type"
          placeholder="选择分类"
          @click="showPicker = true"
      />
      <van-popup v-model="showPicker" round position="bottom">
        <van-picker
            show-toolbar
            :columns="columns"
            @cancel="showPicker = false"
            @confirm="onConfirm"
        />
      </van-popup>
      <van-search  @search="onSearch" v-model="keyword" placeholder="请输入搜索关键词" />
    </div>

    <van-swipe style="height: 20rem" vertical>
      <van-swipe-item>
        <div class="banner">
          <img :src="require('./../../../common/images/adminLogin.gif')" alt="">
        </div>
      </van-swipe-item>
    </van-swipe>
    <van-cell title="技术(软件)/信息技术" @click="$router.push('/user/workbookList?kind=技术(软件)/信息技术')" is-link value="更多"/>
    <div class="workbook-list">
      <workbookItem v-for="item in oneList" :key="item.workbookInfo.id" :workbook-item="item"/>
      <van-divider v-if="oneList.length === 0" dashed>{{ oneList.length === 0 ? '暂无相关题集' : '' }}
      </van-divider>
    </div>
    <van-cell style="margin-top: 1rem;" title="技术(硬件)/电子信息" is-link value="更多"
              @click="$router.push('/user/workbookList?kind=技术(硬件)/电子信息')"/>
    <workbookItem v-for="item in twoList" :key="item.workbookInfo.id" :workbook-item="item"/>
    <van-divider v-if="twoList.length === 0" dashed>{{ twoList.length === 0 ? '暂无相关题集' : '' }}
    </van-divider>
    <van-cell @click="$router.push('/user/workbookList?kind=机械/汽车制造')" style="margin-top: 1rem;" title="机械/汽车制造" is-link
              value="更多"/>
    <workbookItem v-for="item in threeList" :key="item.workbookInfo.id" :workbook-item="item"/>
    <van-divider v-if="threeList.length === 0" dashed>
      {{ threeList.length === 0 ? '暂无相关题集' : '' }}
    </van-divider>
    <van-cell @click="$router.push('/user/workbookList?kind=产品/运营/游戏策划/设计')" style="margin-top: 1rem;"
              title="产品/运营/游戏策划/设计" is-link value="更多"/>
    <workbookItem v-for="item in fourList" :key="item.workbookInfo.id" :workbook-item="item"/>
    <van-divider v-if="fourList.length === 0" dashed>{{ fourList.length === 0 ? '暂无相关题集' : '' }}
    </van-divider>

    <van-cell @click="$router.push('/user/workbookList?kind=财务财会/经济学/金融学')" style="margin-top: 1rem;" title="财务财会/经济学/金融学"
              is-link value="更多"/>
    <workbookItem v-for="item in fiveList" :key="item.workbookInfo.id" :workbook-item="item"/>
    <van-divider v-if="fiveList.length === 0" dashed>{{ fiveList.length === 0 ? '暂无相关题集' : '' }}
    </van-divider>

    <van-cell @click="$router.push('/user/workbookList?kind=市场营销')" style="margin-top: 1rem;" title="市场营销" is-link
              value="更多"/>
    <workbookItem v-for="item in sixList" :key="item.workbookInfo.id" :workbook-item="item"/>
    <van-divider v-if="sixList.length === 0" dashed>{{ sixList.length === 0 ? '暂无相关题集' : '' }}
    </van-divider>

    <van-cell @click="$router.push('/user/workbookList?kind=管理类')" style="margin-top: 1rem;" title="管理类" is-link
              value="更多"/>
    <workbookItem v-for="item in sevenList" :key="item.workbookInfo.id" :workbook-item="item"/>
    <van-divider v-if="sevenList.length === 0" dashed>{{ sevenList.length === 0 ? '暂无相关题集' : '' }}
    </van-divider>

    <van-cell @click="$router.push('/user/workbookList?kind=职能类')" style="margin-top: 1rem;" title="职能类" is-link
              value="更多"/>
    <workbookItem v-for="item in eightList" :key="item.workbookInfo.id" :workbook-item="item"/>
    <van-divider v-if="eightList.length === 0" dashed>{{ eightList.length === 0 ? '暂无相关题集' : '' }}
    </van-divider>
  </div>
</template>

<script>
import workbookItem from "../../../components/workbookItem/workbookItem";
import {getWorkBookList} from "@/api";
import {Toast} from 'vant'
import moment from "moment";
import {workbookTypeList} from "@/data";

export default {
  name: "Home",
  data() {
    return {
      oneList: [],
      twoList: [],
      threeList: [],
      fourList: [],
      fiveList: [],
      sixList: [],
      sevenList: [],
      eightList: [],
      keyword: '',
      type: '技术(软件)/信息技术',
      showPicker: false,
      columns: workbookTypeList.map(i => i.value),

    }
  },
  components: {
    workbookItem
  },
  mounted() {
    this.getWorkBookList('技术(软件)/信息技术', 'oneList');
    this.getWorkBookList('技术(硬件)/电子信息', 'twoList');
    this.getWorkBookList('机械/汽车制造', 'threeList');
    this.getWorkBookList('产品/运营/游戏策划/设计', 'fourList');
    this.getWorkBookList('财务财会/经济学/金融学', 'fiveList');
    this.getWorkBookList('市场营销', 'sixList');
    this.getWorkBookList('管理类', 'sevenList');
    this.getWorkBookList('职能类', 'eightList');
  },
  methods: {
    onConfirm(value) {
      this.type = value;
      this.showPicker = false;
    },
    onSearch(){
      // eslint-disable-next-line no-console
      console.log(this.keyword, this.type);
      this.$router.push(`/user/workbookList?kind=${this.type}&keyword=${this.keyword}`)
    },
    async getWorkBookList(keyWord = '', key) {
      if(key === 'oneList'){
        Toast.loading({
          duration: 0, // 持续展示 toast
          message: '加载中...',
          forbidClick: true,
          loadingType: 'spinner'
        });
      }
      let data = await getWorkBookList({
        keyWord
      });
      Toast.clear();
      if (data.code === 200) {
        let workbookList = [];
        data.result.workbookList.forEach(item => {
          let {workbookInfo, testList} = item;
          workbookInfo.createdAt = moment(workbookInfo.createdAt).format('YY/MM/DD HH:mm');
          workbookList.push({
            workbookInfo,
            testList
          })
        });
        this[key] = workbookList;
        // eslint-disable-next-line no-console
        console.log(workbookList);
      } else {
        Toast('获取失败');
      }
    },
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
>>> .van-nav-bar {
  background-color: #1989fa
}

>>> .van-nav-bar__title {
  color white
}

.banner {
  width: 100%
  height: 20rem
  background-color: #f9f9f9

  > img {
    width: 100%
    height: 20rem
    object-fit cover
  }
}
</style>
